.footer-box {
	background:#f5f5f5;
	margin:0 -15px;
	// 20200325 START 박지영 : redmine 6615
	word-break:normal;
	word-wrap: normal;
	// 20200325 END
	[dir="rtl"] & {
		 text-align: right;
	}
	@include screen(print) {
		display:none;
	}
	.skip_nav {
		max-width:1280px;
		margin:0 auto;
	}
	.footer-component {
		max-width:(1284px + 60px);
		margin:0 auto;
		padding: 0 30px;
		@include screen(custom, max, $max-sm){
			padding:0 24px;
		}
		.footer-caution {
			font-size:14px;
			line-height:20px;
			padding:25px 0;
			border-bottom:1px solid $line-gray;
			color:$color-dimgray;
			.caution-header {
				padding-bottom:25px;
				margin-bottom:25px;
				border-bottom:1px solid $line-gray;

			}
			a {
				color:$color-carmine;
				text-decoration:underline;
			}
			p {
				margin:0 0 19px;
				&:last-child {
					margin:0;
				}
			}
			strong, b {
				@include font-family($font-bold);
			}
			h1, h2, h3, h4, h5, h6 {
				font-size: 16px;
				font-weight: 600;
				color: #333;
				line-height: 24px;
				padding-bottom: 2px;
				@include screen(custom, max, $max-sm){
					padding-bottom: 7px;
				}
			}
		}
		.sub-contents.social {
			@extend %clearfix;
			padding:20px 0;
			border-bottom:1px solid $line-gray;
			@include screen(custom, max, $max-sm){
				padding:20px 0 18px;
			}
			.country-information-wrap{
				@include screen(custom, min, $min-md){
					float: left;
					padding-right:5px;
					[dir="rtl"] & {
						float: right;
						padding-right:0;
						padding-left:5px;
					}
				}
				@include screen(custom, max, $max-sm){
					&:only-child {
						padding-bottom:5px;
					}
				}
				.country-information {
					position:relative;
					padding:5px 0 0;
					display:inline-block;
					font-size:0;
					.icon-wrap{
						position:relative;
						display:inline-block;
						vertical-align:middle;
						line-height:25px;
						.flag-icon{
							width:19px;
							height:19px;
							display:block;
							border-radius:19px;
							margin-right:11px;
							[dir="rtl"] & {
								margin-right:0;
								margin-left:11px;
							}
						}
						.earth-icon{
							width:20px;
							height:20px;
							display:block;
							margin-right: 9px;
							[dir="rtl"] & {
								margin-left: 9px;
								margin-right: 0;
							}
							cursor:pointer;
							&:hover+.icon-tooltip{
								display:block;
							}
						}
						.icon-tooltip{
							display:none;
							width: 272px;
							position: absolute;
							z-index: 1000;
							bottom: 40px;
    						left: -14px;
							[dir="rtl"] & {
								left: auto;
								right: -14px;
							}
							&:before{
								position: absolute;
							    content: '';
							    border-color: #e4e4e4;
							    border-style: solid;
							    border-width: 0 0 1px 1px;
							    background: #fff;
							    width: 11px;
							    height: 11px;
								bottom: -5px;
								left: 18px;
								[dir="rtl"] & {
									left:auto;
									right:18px;
								}
								transform: rotate(-45deg);
							}
							.tooltip-inner{
								padding: 18px 24px;
								border-radius: .25rem;
							}
						}
					}

					>a{
						font-size:16px;
						line-height:25px;
						@include font-family($font-semibold);
						display:inline-block;
						vertical-align:middle;
					}
					.tooltip-area{
						min-width:125px;
						&.top-center{
							margin-left:0;
							transform: translateX(-50%);
						}
						.tooltip-inner{
							padding:5px 10px;
							a{
								color:#333333;
								font-size:14px;
								line-height:20px;
								@include font-family($font-regular);

							}
						}
					}
				}
			}
			.social-links {
				@include screen(custom, min, $min-md){
					text-align:right;
					[dir="rtl"] & {text-align:left;}
				}
				@include screen(custom, max, $max-sm){
					margin-top:26px;
				}
				li {
					position:relative;
					display:inline;
					@include screen(custom, max, $max-sm) {
						&:last-child {
							.social-icon {
								margin-right:0;
							}
						}
					}
					.tooltip-area{
						min-width:160px;
						.tooltip-inner{
							padding:24px 34px 24px 24px;
							a{
								display:block;
							}
						}

					}
				}
				.social-icon {
					position:relative;
					width:35px;
					height:35px;
					display:inline-block;
					margin-left:8px;
					[dir="rtl"] & {
						margin-left:0;
						margin-right:8px;
					}
					@include screen(custom, max, $max-lg){
						margin-bottom:12px;
						margin-left:0px;
						margin-right:8px;
						[dir="rtl"] & {
							margin-left:8px;
							margin-right:0px;
						}
					}
					img {width:100%;height:auto;}
					&.icon-facebook {
						background:url('/lg5-common-gp/images/common/icons/facebook-circle-fill.svg') left top no-repeat;
					}
					&.icon-twitter {
						background:url('/lg5-common-gp/images/common/icons/twitter-circle-fill.svg') left top no-repeat;
					}
					&.icon-google {
						background:url('/lg5-common-gp/images/common/icons/google-circle-fill.svg') left top no-repeat;
					}
					&.icon-pinterest {
						background:url('/lg5-common-gp/images/common/icons/pinterest-circle-fill.svg') left top no-repeat;
					}
					&.icon-youtube {
						background:url('/lg5-common-gp/images/common/icons/youtube-circle-fill.svg') left top no-repeat;
					}
					&.icon-email {
						background:url('/lg5-common-gp/images/common/icons/email-circle-fill.svg') left top no-repeat;
					}
					&.icon-instagram {
						background:url('/lg5-common-gp/images/common/icons/instagram-circle-fill.svg') left top no-repeat;
					}
					&.icon-linked-in {
						background:url('/lg5-common-gp/images/common/icons/linked-in-fill.svg') left top no-repeat;
					}
					&.icon-spiceworks {
						background:url('/lg5-common-gp/images/common/icons/spiceworks-fill.svg') left top no-repeat;
					}
					&.icon-snapchat {
						background:url('/lg5-common-gp/images/common/icons/snapchat-fill.svg') left top no-repeat;
					}
					// 20200421 START 이상현 - CN Wechat ui 추가. 디바이스 별 인터페이스 별도 적용.
					&.mobile {
						display:none;
						@include screen(custom, max, $max-sm){
							display:inline-block;
						}
					}
					&.desktop {
						display:inline-block;
						@include screen(custom, max, $max-sm){
							display:none;
						}
					}
					// 20200421 END
				}
			}
		}
		.footer-contents {
			@extend %clearfix;
			padding-top:30px;
			padding-bottom:50px;
			display:table;
			// 20200409 START 박지영 - footer 데이터 없는 경우 디자인 오류 수정
			width:100%;
			// 20200409 END
			@include screen(custom, max, $max-lg) {
				width:100%;
				display:block;
			}
			.no-link {
				font-size:14px;
				line-height:16px;
				color:$color-dimgray;
			}
			a {
				font-size:14px;
				line-height:16px;
				color:$color-dimgray;
				&.social-support-fb {
					display:inline-block;
					line-height:20px;
					&:before {
						display:inline-block;
						content:'';
						margin-right:8px;
						[dir="rtl"] & {
							margin-right:0;
							margin-left:8px;
						}
						width:20px;
						height:20px;
						vertical-align:middle;
						background:url('/lg5-common-gp/images/common/icons/facebook-circle-line-m.svg') left top no-repeat;
					}
				}
				&.social-support-tw {
					display:inline-block;
					line-height:20px;
					&:before {
						display:inline-block;
						content:'';
						margin-right:8px;
						[dir="rtl"] & {
							margin-right:0;
							margin-left:8px;
						}
						width:20px;
						height:20px;
						vertical-align:middle;
						background:url('/lg5-common-gp/images/common/icons/twitter-circle-line-m.svg') left top no-repeat;
					}
				}
			}
			ul {
				margin-bottom:0;

				li {
					margin-bottom:8px;
					&.social {
						margin-bottom:4px;
						a {
							color:$color-nightrider;
						}
					}
					&:last-child {
						margin-bottom:0;
					}
				}
			}
			.footer-title {
				margin-bottom:12px;
				.no-link,
				a,
				h5 { // LGECI-307 20201124 웹접근성 
					font-size:17px;
					line-height:21px;
					@include font-family($font-bold);
					color:$color-nightrider;
					// 20200325 START 박지영 : redmine 6615
					word-break:normal;
					word-wrap: normal;
					// 20200325 END
				}
				+ .footer-title {
					margin-top:24px;
				}
				+ .support-text {
					+ .footer-title {
						margin-top:29px;
					}
				}
				&:last-child {
					margin-bottom:0;
				}
			}
			.footer-sub-title {
				> .no-link,
				> a,
				h6 { // LGECI-307 20201124 웹접근성 
					font-size:16px;
					line-height:24px;
					@include font-family($font-regular);
					color:$color-black;
				}
				ul {
					margin-top:4px;
					margin-bottom:11px;
					li {
						padding-left:10px;
						[dir="rtl"] & {
							padding-left:0;
							padding-right:10px;
						}
					}
					//LGEIS-321 start
					&.paylogo{
						li{
							padding-left: 0;
							display: inline-block;
							margin-right: 2px;
							margin-bottom: 3px;
						}
					}
					//LGEIS-321 end
				}
			}
			.support-text {
				+ .support-text {
					margin-top:26px;
				}
				+ .link-text {
					margin-top:32px;
				}
				+ .footer-title {
					margin-top:24px;
				}
			}
			ul {
				+ .footer-title {
					margin-top:28px;
				}
			}


			.link-bottom {
				.link-wrap {
					margin-bottom:0;
				}
			}
			.footer-contents-box {
				width:16.978%;
				font-size:0;
				display:table-cell;
				// 20200429 START 이상현 - rtl 국가에서 footer 모바일 화면 수정
				vertical-align:top;
				@include screen("xl"){
					[dir="ltr"] & {
						border-right:1px solid $line-gray;
					}
					[dir="rtl"] & {
						border-left:1px solid $line-gray;
					}
				}
				.link-wrap {
					width:25%;
					display:inline-block;
					vertical-align:top;
					margin-bottom:25px;
					//&:nth-child(5),
					//&:nth-child(6),
					//&:nth-child(7),
					//&:nth-child(8){
					//	margin-bottom:0;
					//}
					/*
					.link-about-lg {
						margin-top:55px;
						@include screen(custom, max, $max-lg) {
							margin-top:0;
						}
					}
					*/
				}
				[dir="ltr"] &:not(:last-child) .link-wrap {
					padding-right:25px;
				}
				[dir="rtl"] &:not(:last-child) .link-wrap {
					padding-left:25px;
				}
				// 20200429 END
				.box-wrap {
					+ .box-wrap {
						margin-top:24px;
					}
				}
				&.column-4 {
					width:65.9%;
					.box-wrap {
						+ .box-wrap {
							margin-top:0px;
							.link-wrap {
								margin-bottom:0;
							}
						}
					}
				}
				&.column-1 {
					.link-wrap {
						width:100%;
						[dir="ltr"] & {
							padding-left:25px;
						}
						[dir="rtl"] & {
							padding-right:25px;
						}
						@include screen(custom, max, $max-lg) {
							padding-left:0;
							padding-right:0;
							width:25%;
							//margin-bottom:0;
							[dir="ltr"] & {
								padding-left:0;
							}
							[dir="rtl"] & {
								padding-right:0;
							}
							.box-wrap {
								+ .box-wrap {
									margin-top:0;
								}
								width:100%;
								[dir="ltr"] & {
									padding-right:25px;
								}
								[dir="rtl"] & {
									padding-left:25px;
								}
								display:inline-block;
								vertical-align:top;
							}
						}
					}
					ul {
						padding-bottom:5px;
					}
					.support-text {
						display:inline-block;

					}
					/*
					&:last-child {
						@include screen(custom, max, $max-lg) {
							.box-wrap {
								width:25%;
								&:nth-child(2){
									width:25%;
									.support-text {
										width:100%;
										+ .support-text {
											margin-top:23px;
										}
										+ .link-text {
											margin-top:0px;
											vertical-align:top;
											line-height:16px;
										}
									}
								}
							}
						}
						@include screen(custom, max, $max-md) {
							.box-wrap {
								width:25%;
								&:nth-child(2){
									width:50%;
									.support-text {
										width:50%;
										+ .support-text {
											margin-top:0px;
										}
										+ .link-text {
											margin-top:0px;
											vertical-align:top;
											line-height:16px;
										}
									}
								}
								&:last-child {
									margin-top:24px;
								}
							}
						}
					}
					*/
				}
				span:not(.no-link) {
					display:block;
					font-size:14px;
					line-height:16px;
					margin-bottom:8px;
					color:$color-nightrider;
					&:last-child {
						margin-bottom:0;
					}
				}
				@include screen(custom, max, $max-lg) {
					width:100%;
					display:block;
					border-right:0;
					border-bottom:1px solid $line-gray;
					padding-bottom:19px;
					margin-bottom:22px;
					&:last-child {
						border-bottom:0;
						margin-bottom:0;
						padding-bottom:0;
					}
					&.column-4 {
						width:100%;

					}

				}
				.ico-right {
					line-height:24px;
					&:after {
						background:url('/lg5-common-gp/images/common/icons/page-right.svg') 0 1px no-repeat;
					}
				}
				.ico-blank {
					text-decoration:none;
					&:hover {
						text-decoration:underline;
					}
					&:after {
						background:url('/lg5-common-gp/images/common/icons/blank-dimgray.svg') 0 0 no-repeat;
					}
				}
				//&:nth-child(2) {
				//	.link-wrap {
				//		padding-right:0;
				//	}
				//}
				&:last-child {
					[dir="ltr"] & {
						border-right:0;
					}
					[dir="rtl"] & {
						border-left:0;
					}
					.link-wrap {
						padding-right:0;
						&:first-child {
							.box-wrap {
								.footer-title {
									&:last-child {
										margin-bottom:0;
									}
								}
							}
						}
					}
					@include screen(custom, max, $max-lg) {
						.link-wrap {
							//width:100%;
							/*
							.box-wrap {
								width:100%;
								.support-text {
									width:160px;
									margin-top:0;

								}
								.ico-right {
									margin-top:0;
									vertical-align:top;
								}
							}
							*/
						}
					}
				}
			}
		}
	}
	.footer-bottom-box {
		background:$bg-nightrider;
		.footer-bottom {
			margin:0 15px;
			padding:0 15px;
			a {
				&.ico-blank {
					&:after {
						@extend %linkicon;margin-left:6px;
						background:url('/lg5-common-gp/images/common/icons/blank-white.svg') no-repeat 0 0;
					}
				}
			}
			&.banner-count1 .footer-component .bottom-links {
				.links-left {
					width:calc(100%-250px);
				}
				@include screen(custom, max, 950) {
					.links-left {width: 100%;}
					.links-right {
						position: relative;
						width:100%;
						top:auto;
						left:auto;
						transform: none;
						margin-top: 17px;
					}
				}
			}
			&.banner-count2 .footer-component .bottom-links {
				.links-left {
					width:calc(100%-500px);
				}
				@include screen(custom, max, 1080) {
					.links-left {width: 100%;}
					.links-right {
						position: relative;
						width:100%;
						top:auto;
						left:auto;
						transform: none;
						margin-top: 17px;
					}
				}
			}
			&.banner-count3 .footer-component .bottom-links {
				.links-left {
					width:calc(100%-750px);
				}
				@include screen(custom, max, 1155) {
					.links-left {width: 100%;}
					.links-right {
						position: relative;
						width:100%;
						top:auto;
						left:auto;
						transform: none;
						margin-top: 17px;
					}
				}
			}
			&.banner-count4 .footer-component .bottom-links {
				.links-left {
					width:calc(100%-1000px);
				}
				@include screen(custom, max, $max-lg) {
					.links-left {width: 100%;}
					.links-right {
						position: relative;
						width:100%;
						top:auto;
						left:auto;
						transform: none;
						margin-top: 17px;
					}
				}
			}
			
			.footer-component {
				max-width:1284px;
				margin:0 auto;
				padding:22px 0;
				font-size:0;
				.bottom-links {
					position:relative;
					min-height: 58px;
					.links-left {
						width:100%;
						min-width:364px;
						ul{
							@extend %clearfix;
							li{
								position: relative;
								float: left;
								margin-right:30px;
								[dir="rtl"] & {
									float: right;
									margin-right:0;
									margin-left:30px;
								}
								line-height: 18px;
								/*
								&:first-child {
									margin-right:40px;
									[dir="rtl"] & {
										margin-right:0;
										margin-left:40px;
									}
									&:after {
										@include pseudo();
										position:absolute;
										right:-20px;
										top:50%;
										[dir="rtl"] & {
											right:auto;
											left:-20px;
										}
										margin-top:-5px;
										width:1px;
										height:10px;
										background:$bg-dimgray;
									}
								}
								*/
							}
						}
						a {
							font-size:14px;
							line-height:24px;
							color:$color-white;
							@include font-family($font-regular);
							display:inline-block;
							position:relative;
						}
					}
					.links-right {
						position:absolute;
						right:0;
						[dir="rtl"] & {
							right:auto;
							left:0;
						}
						top: 0;
						a {
							font-size:14px;
							line-height:19px;
							color:$color-nightrider;
							@include font-family($font-regular);
							text-transform:none;
							display:inline-block;
							vertical-align:middle;
							width:240px;
							height:58px;
							border-radius:3px 3px;
							padding: 9px 0 0 62px;
							word-break: normal;
							background:$bg-white url('/lg5-common-gp/images/common/footer/icon-jungdo.jpg') 0 0 no-repeat;
							[data-countrycode=cn] &,
							[data-countrycode=tw] &,
							[data-countrycode=jp] & {
								font-size:13px;
							}
							// 20200429 START 이상현 - footer 정도경영 배너 ltr 화면 고정.
							&[data-link-area="footer-jeongdo_management"] {
								direction:ltr;
								text-align:left;
								&:after {
									margin: 0 0 0 3px;
									transform: none;
								}
							}
