	// 20200421 START 이상현 - CN Wechat ui 추가. 디바이스 별 인터페이스 별도 적용.
	// Icon layer popup
	.local-layer {
		display:none;
		background: $bg-white;
		border: 1px solid $line-gray;
		border-radius: 3px;
		margin-top: 30px;
		position: absolute;
		z-index:1;
		top: 100%;
		right:-1px;
		padding: 24px 34px 24px 24px;
		[dir="rtl"] & {
			right:auto;
			left:-1px;
			padding: 24px 24px 24px 34px;
		}
		&.active {
			display:block;
		}
		&:before {
			content:'';
			width: 11px;
			height: 11px;
			border-color: $line-gray;
			border-style: solid;
			border-width: 1px 1px 0 0;
			background: $bg-white;
			transform: rotate(-45deg);
			position: absolute;
			top:-6px;
			right:12px;
			[dir="rtl"] & {
				right:auto;
				left:12px;
			}
		}
		img {max-width:100%;}
		// QR CODE
		.qr-image {
			width: 100px;
			height: 100px;
			background:$color-nightrider;
		}
		.btn-close {
			position: absolute;
			top: 12px;
			border: none;
			background: url("/lg5-common-gp/images/common/icons/popover-close.svg");
			width: 12px;
			height: 12px;
			right: 12px;
			[dir="rtl"] & {
				right:auto;
				left:12px;
			}
		}
	}
	// 20200421 END
}
