										vertical-align:top;
										a {
											margin-top:0;
											line-height:22px;
										}
									}
								}
							}
						}
					}
				}

			}
		}
		.footer-bottom-box {
			.connect-link {
				margin-top:5px;
				a {
					font-size:14px;
					line-height:24px;
					color:#b2b2b2;
					text-decoration:underline;
				}
			}
			.footer-bottom {
				.footer-component {
					.links-left {
						a {
							margin-bottom:3px;
						}
					}
					.copyright {
						margin-top:2px;
					}
					@include screen(custom, max, $max-md) {
						.bottom-links {
							position:relative;

							.links-left {
								a {
									margin-bottom:5px;
								}
							}
							.links-right {
								top:0;
								transform:none;
							}
						}
						.copyright {
							margin-top:0;
						}
					}
					@include screen(custom, max, $max-sm) {
						padding-bottom:25px;
						.bottom-links {
							margin-bottom:10px;
						}

						.connect-link {
							margin-top:8px;
						}
					}
				}
			}
		}
	}

	// mobile view, 2depth, 3depth
	.visible-mobile {
		.footer-component {
			.footer-contents {
				ul {
					li {
						padding-left:10px;
						[dir="rtl"] & {
							padding-left:0;
							padding-right:10px;
						}
						&.depth-title {
							padding-left:0;
							[dir="rtl"] & {
								padding-right:0;
							}
							.no-link,
							a {
								@include font-family($font-semibold);
								font-size:16px;
								line-height:20px;
								color:$color-black;
								display:inline-block;
								position:relative;
							}
							a {
								&:hover {
									text-decoration:underline;
								}
							}
						}
					}
					li + li.depth-title {
						padding-top:0;
					}
				}
			}
		}
	}
}