.band-scroll {
	position:relative;
	overflow: hidden;
}
.the-search-window {
	// compoent set
	$wrap-width:848px;
	// 20200309 START 이상현 - 스크롤 바 제거
	// $scroll-position-adjust:8px;	// secondary view divide line position-y 
	// 20200309 END 이상현
	@mixin shorten{
		white-space:nowrap;
		text-overflow:ellipsis;
		overflow:hidden;
	}
	@mixin flex($direction:row) {
		display: -webkit-box;
		display: -moz-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display:flex;
		-webkit-flex-direction: $direction;
		-ms-flex-direction: $direction;
		flex-direction: $direction;
		-webkit-box-lines: single; 
		-ms-flex-wrap: wrap;
		flex-wrap:wrap;
	}
	// reset
	button {padding:0;border:none; background:none;}
	.link-text {
		&.color-nightrider {color:$color-nightrider;}
	}

	// 20200429 START 이상현 - gnb search rtl 화면 오류 수정
	display:none;
	padding:0 15px;
	box-sizing:content-box;
	width:100%;
	height:100vh;
	overflow:auto;
	background-color:hsla(0, 100%, 100%, 0.98);
	position: absolute;
	z-index:100;
	top:0;
	left:-15px;
	[dir="rtl"] & {left:-25px;}
	// 20200429 END
	// active
	&.active {
		display:block;
	}
	// top margin
	&:before {
		content:"";
		display:block;
		width:100%;
		@include screen(custom, min, $min-md){
			height:100px;
			@media screen and (max-height:768px){
				height:100/(768*0.01) * 1vh;
			}
		}
		@include screen(custom, min, $min-lg){
			height:150px;
			@media screen and (max-height:768px){
				height:150/(768*0.01) * 1vh;
			}
		}
		@include screen(custom, max, $max-sm){
			height:50px;
		}
	}
	// non active button
	.btn-close-cover {
		position: absolute;
		top:56px; right:45px;
		[dir="rtl"] & {
			right:auto;
			left:45px;
		}
		@media screen and (max-height:768px){
			top:56px;
			top:6.51vh;
		}
		@include screen(custom, max, $max-sm){
			top:14px; right:20px;
			[dir="rtl"] & {left:20px;}
		}
		.btn-close-search-window {
			vertical-align:top;
			width:20px;
			height:20px;
			background : {
				image:url(/lg5-common-gp/images/common/icons/modal-close.svg);
				repeat: no-repeat;
				position:0 0;
				size:100% auto;
			}
			cursor:pointer;
			@include screen(custom, max, $max-sm){
				width:16px;
				height:16px;
			}
		}
	}
	.search-window-wrap {
		max-width:$wrap-width + (69px*2);
		padding:0 69px;
		margin:0 auto;
		@include screen(custom, max, $max-sm){
			padding:0 9px;
		}
	}
	.title {
		text-align:center;
		font-size:40px;
		line-height:42px;
		color:$color-black;
		@include font-family($font-semibold);
		margin:-9px 0 51px;
		@include screen(custom, max, $max-sm){
			display:none;
		}
	}
	// primary view
	.keyword-field {
		display:none;
		@extend %clearfix;
		&.active {
			display:block;
		}
	}
	.input-keyword-field {
		// variable
		$input-field-height:58px;
		$search-button-size:24px;
		// reset
		form {height:100%;}
		button {
			position: absolute;
			width:$search-button-size; 
			height:$search-button-size;
			background : {
				position:50% 50%;
				repeat:no-repeat;
			}
			cursor: pointer;
		}

		position:relative; z-index:10;
		width:100%;
		height:$input-field-height;
		text-align:center;
		@include screen(custom, max, $max-sm){
			height:$input-field-height - 14px;
		}
		.input-field {
			position: relative;
			height:100%;
			background-color:$bg-white;
			input[type="text"] {
				width:100%;
				height:100%;
				background-color:transparent;
				padding:0 79px;
				border:2px solid $line-black;
				vertical-align: top;
				text-align:center;
				font-size:24px;
				color:$color-black;
				@include font-family($font-semibold);
				@include placeholder($color-gray);
				&:focus {
					outline:none;
					@include placeholder(transparent);
				}
				@include screen(custom, max, $max-sm){
					font-size:20px;
					text-align:left;
					padding: 0 70px 0 10px;
					[dir="rtl"] & {
						text-align:right;
						padding: 0 10px 0 70px;
					}
				}
			}
			.btn-clear-input {
				display:none;
				background-image:url(/lg5-common-gp/images/common/icons/gnb-search-clear.svg);
				bottom:50%; transform:translateY(50%); 
				right:56px;
				[dir="rtl"] & {
					right:auto;
					left:56px;
				}
				@include screen(custom, max, $max-sm){
					right:44px;
					[dir="rtl"] & {left:44px;}
				}
			}
			// event
			&.is-typing .btn-clear-input {display:block;}
		}
		button[type="submit"] {
			background-image:url(/lg5-common-gp/images/common/icons/gnb-search-carmine.svg);
			background-size:100% auto;
			bottom:50%; transform:translateY(50%); 
			right:20px;
			[dir="rtl"] & {
				right:auto;
				left:20px;
			}
			@include screen(custom, max, $max-sm){
				width:20px; height:20px;
				right:15px;
				[dir="rtl"] & {left:15px;}
			}
		}
	}
	// recommended searched
	.rolling-keyword-area-wrap {
		position:relative;
		margin:20px 0 84px;
		padding-bottom:24px;
		@include screen(custom, max, $max-sm){
			margin:14px 0 0;
		}
		&:after {
			content:"";
			position: absolute;
			bottom:0;
			left:50%; margin-left:-1px;
			width:2px;
			height:14px;
			background : {
				image:url(/lg5-common-gp/images/common/icons/item-more.svg);
				repeat:no-repeat;
				position:0 0;
			}
		}
	}
	.rolling-keyword-area {
		// variable
		$rolling-item-height:31px;
		$exposal-length:3;

		text-align:center;
		overflow: hidden;
		max-height:$rolling-item-height * $exposal-length;
		@include screen(custom, max, $max-sm){
			max-height:($rolling-item-height - 1px) * $exposal-length;
			margin-top:10px;
		}
		.rolling-keyword {
			height:$rolling-item-height;
			font-size:16px;
			line-height:$rolling-item-height;
			color:$color-gray;
			transition:color 300ms ease;
			@include font-family($font-semibold);
			@include shorten;
			@include screen(custom, max, $max-sm){
				height:$rolling-item-height - 1px;
				font-size:15px;
				line-height:$rolling-item-height - 1px;
			}
			// center item
			&.highlight {color:$color-nightrider;}
			a {
				font-size:inherit;
				line-height:inherit;
				color:inherit;
			}
		}
	}
	// recently searched & most searched common
	.recenlty-keyword-list,
	.most-searched-board {
		@include screen(custom, min, $min-md){
			width:50%;
			height:150px;
			margin-bottom:30px;	// prepare screen height less 769px
			float:left;
			[dir="rtl"] & {float:right;}
		}
		.head {
			font-size:16px;
			line-height:24px;
			color:$color-dimgray;
			@include screen(custom, max, $max-sm){
				font-size:14px;
			}
		}
	}
	// recently searched
	.recenlty-keyword-list {
		position: relative;
		[dir="rtl"] & {text-align:right;}
		@include screen(custom, min, $min-md){
			padding-right:30px;
			[dir="rtl"] & {
				padding-right:0;
				padding-left:30px;
			}
			// 20200429 START 이상현 - gnb search rtl 화면 오류 수정
			&:after{
				content:"";
				top:6px; 
				width:1px;
				height:147px;
				background-color:$line-lightgray;
				position: absolute;
				left:100%;
				[dir="rtl"] & {
					left:0%;
				}
			}
			// 20200429 END
		}
		@include screen(custom, min, $min-lg){
			padding-right:50px;
			[dir="rtl"] & {padding-left:50px;}
		}
		@include screen(custom, max, $max-sm){
			border-bottom:1px solid $line-lightgray;
			padding-bottom:19px;
			margin-top:22px;
		}
		ul {
			display:none;
			margin-top:7px;
			&.active{display:block;}
			li {
				padding-right:(50px/($wrap-width/2)) * 100%;
				[dir="rtl"] & {
					padding-right:0;
					padding-left:(50px/($wrap-width/2)) * 100%;
				}
				@include shorten;
				@include screen(custom, max, $max-sm){
					margin-bottom:3px;
				}
			}
			a {
				font-size:16px;
				line-height:32px;
				color:$color-nightrider;
				@include font-family($font-semibold);
			}
		}
		.insertview {
			display:none;
			font-size:14px;
			line-height:20px;
			color:$color-dimgray;
			margin-top:20px;
			&.active{display:block;}
		}
		.no-history {
			&:before{
				content:"";
				display:inline-block;
				vertical-align:top;
				width:20px; height:20px;
				background : {
					image : url(/lg5-common-gp/images/common/icons/clock-gray.svg);
					repeat : no-repeat;
					position : 50% 50%;
				}
				margin-right:7px;
				[dir="rtl"] & {
					margin-right:0;
					margin-left:7px;
				}
			}
			@include screen(custom, max, $max-sm){
				text-align:center;
				margin-top:22px;
			}
		}
		.disagree-cookie {
			text-align:center;
			@include screen(custom, max, $max-sm){
				margin-top:17px;
			}
			.link-text {
				color:$color-carmine;
			}
		}
		.btn-clear{
			display:none;
			@include screen(custom, min, 360){
				position: absolute;
				right:0; top:-2px;
			}
			@include screen(custom, min, $min-md){
				right:30px;
			}
			@include screen(custom, min, $min-lg){
				right:50px;     
			}
			@include screen(custom, max, 359){
				text-align: right;
				margin-top: -7px;
			}
			[dir="rtl"] & {
				right:auto;
				@include screen(custom, min, 360){
					left:0;
				}
				@include screen(custom, min, $min-md){
					left:30px;
				}
				@include screen(custom, min, $min-lg){
					left:50px;     
				}
				@include screen(custom, max, 359){
					text-align: left;
				}
			}
		}
		ul.active ~ .btn-clear {display:block;}
	}
	// most searched
	.most-searched-board {
		$tag-height:28px;
		$tag-margin-bottom:12px;
		
		overflow:hidden;
		[dir="rtl"] & {text-align:right;}
		@include screen(custom, min, $min-md){
			padding-left:30px + 1px;
			[dir="rtl"] & {
				padding-left:0;
				padding-right:30px + 1px;
			}
		}
		@include screen(custom, min, $min-lg){
			padding-left:50px + 1px;
			[dir="rtl"] & {
				padding-left:0;
				padding-right:50px + 1px;
			}
		}
		@include screen(custom, max, $max-sm){
			margin-top: 17px;
		}
		.stick-words {
			height:($tag-height + $tag-margin-bottom) * 4;
			margin-top:17px;
			@include screen(custom, max, $max-sm){
				margin-top:13px;
			}
		}
		.word-tag {
			display:inline-block;
			vertical-align:top;
			white-space: nowrap;
			overflow: hidden;
			height:$tag-height;
			background:$bg-white;
			font-size:14px;
			color:$color-nightrider;
			@include font-family($font-semibold);
			padding:2px 9px;
			border:1px solid $line-dimgray;
			border-radius:3px;
			margin-bottom: $tag-margin-bottom;
			// 20200514 START 이상현 - gnb search 추천 검색어 rtl 간격 수정
			&:not(:last-child) {
				margin-right: 6px;
				[dir="rtl"] & {
					margin-right: 0;
					margin-left: 6px;
				}
			}
			// 20200514 END
			&:focus, 
			&:hover {
				text-decoration:none;
				border:1px solid $line-black;
				color:$color-black;
			}
			&.cutoff {
				visibility:hidden;
			}
		}
	}
	// secondary view
	.similar-mapping {
		display:none;
		text-align:center;
		font-size:16px;
		line-height:35px;
		color:$color-nightrider;
		@include font-family($font-semibold);
		margin-top:18px;
		@include screen(custom, max, $max-sm){
			font-size:15px;
			line-height:1;
			margin-top:21px;
		}
		a {
			font-size:inherit;
			line-height:inherit;
			color:inherit;
			font-weight:inherit;
		}
		&.active {display:block;}
		.suggestion {
			color:$color-carmine;
		}
	}
	.result-area {
		[dir="rtl"] & {text-align:right;}
		display:none;
		// 20200309 START 이상현 - 레이아웃 오류 수정
		margin-top: 44px;
		margin-bottom:70px;
		@include screen(custom, max, $max-sm){
			margin-top: 16px;
			margin-bottom:30px;
		}
		// 20200309 END 
		&.active {
			display:block;
			@include screen(custom, min, $min-md){
				@include flex;
			}
		}
	}
	// match product results 
	.match-results {
		position: relative;
		// 20200309 START 이상현 - 스크롤 바 제거
		@include screen(custom, min, $min-md){
			width:calc(100% - (218px + 42px));
			&:before{
				content:"";
				width:1px;
				background-color:$line-lightgray;
				position: absolute;
				top:6px; bottom:0; 
				right:0;
				[dir="rtl"] & {
					right:auto;
					left:0;
				}
			}
		}
		// mScrollbar custom : only draggable bar.
		// .mCSB_draggerContainer {
		// 	.mCSB_dragger_bar {
		// 		width:1px;
		// 		margin:0 ($scroll-position-adjust - 1px);
		// 		[dir="rtl"] & {margin:0 $scroll-position-adjust;}
		// 	}
		// 	.mCSB_draggerRail {
		// 		width: 1px;
		// 		background-color:$line-lightgray;
		// 	}
		// }
		// 20200309 END
	}
	.match-keyword-count {
		font-size:16px;
		line-height:1;
		color:$color-gray;
		@include screen(custom, max, $max-sm){
			font-size:14px;
		}
		.count {color:$color-carmine;}
	}
	.match-product-list {
		margin-top:11px;
		// 20200309 START 이상현 - 스크롤 바 제거
		// @include screen(custom, min, $min-md){
		// 	max-height:292px;
		// }
		// @include screen(custom, min, $min-lg){
		// 	max-height:(120px + 35px) * 3;
		// }
		// 20200309 END
		@include screen(custom, max, $max-sm){
			margin-top:7px;
			border-bottom:1px solid $line-lightgray;
			padding-bottom:17px;
		}
		.list-wrap {
			padding-top:11px;
			@include screen(custom, max, $max-sm){
				padding-top:10px;
			}
		}
		ul {
			@include flex;
			@include screen(custom, max, $max-md){
				// 20200309 START 이상현 - 스크롤 바 제거
				padding-right:10px;
				// 20200309 END
				@include screen(custom, min, $min-md){
					padding-bottom:12px;
				}
			}
		}
		.product-item {
			@include flex;
			@include screen(custom, min, $min-lg){
				width:50%;
				&:nth-child(n+3) {margin-top:35px;}
			}
			@include screen(custom, max, $max-md){
				// 20200309 START 이상현 - 레이아웃 오류 수정
				width:100%;	
				// 20200309 END 
				&:not(:first-child) {margin-top:32px;}
			}
			@include screen(custom, max, $max-sm){
				min-height:72px;
				&:not(:first-child) {margin-top:17px;}
				&:nth-child(n+4) {display:none};
			}
		}
		.product-image {
			width:70px;
			height:70px;
			@include screen(custom, max, $max-sm){
				width:65px;
				height:65px;
			}
			img {
				max-width:100%;
				max-height:100%;
				// outline:1px solid lightcoral;
				// outline-offset: -1px;
			}
		}
		.product-summary {
			min-width:156px;
			width:calc(100% - 70px);
			margin-top: -3px;
			padding-left:20px;
			[dir="rtl"] & {
				padding-left:0;
				padding-right:20px;
			}
			@include screen(custom, max, $max-sm){
				width:calc(100% - 65px);
				margin-top: -4px;
			}
			.product-page-linker {
				display:block;
				width:100%;
				@include screen(custom, min, $min-lg){
					max-width:156px;
				}
			}
			.model-display-name {
				font-size:15px;
				line-height:18px;
				color:$color-nightrider;
				@include font-family($font-semibold);
			}
			.user-friendly-name {
				font-size:14px;
				line-height:18px;
				color:$color-dimgray;
				@include font-family($font-semibold);
				display: block;
				display: -webkit-box;
				-webkit-line-clamp: 2; 
				-webkit-box-orient: vertical;
				overflow: hidden;
				text-overflow: ellipsis;
				max-height:18px * 2;
				margin-top:4px;
				// 20200608 START 박지영 - RTL 에서 괄호로 끝나는 경우 깨지는 현상 수정
				[dir="rtl"] & {
					display:block;
				}
				// 20200608 END
			}
			.btn-area {
				margin-top:3px;
				@include screen(custom, min, $min-lg){
					margin-top:5px;
				}
				.link-text {
					@include screen(custom, min, $min-lg){
						display:block;
					}
					@include screen(custom, max, $max-sm){
						margin-right:14px;
						[dir="rtl"] & {
							margin-right:0;
							margin-left:14px;
						}
					}
				}
			}
		}
	}
	// categorized results
	.categorized-results {
		@include screen(custom, min, $min-md){
			width:218px;
			padding-top:4px;
		}
		@include screen(custom, max, $max-sm){
			margin-top:21px;
		}
		// 20200309 START 이상현 - 스크롤 바 제거
		.head {
			font-size:16px;
			line-height:1;
			color:$color-gray;
			// 20200506 START 이상현 - gnb search rtl 화면 오류 수정
			@include screen(custom, min, $min-md){
				padding-left:42px;
				[dir="rtl"] & {
					padding-left:0;
					padding-right:42px;
				}
			}
			@include screen(custom, max, $max-sm){
				font-size:14px;
			}
			// 20200506 end
		}
		// 20200309 end
		ul {
			margin-top:16px;
			@include screen(custom, max, $max-sm){
				margin:12px 0 0;
			}
		}
	}
	.category-link-name a {
		display:block;
		//background-color:$bg-white;
		padding:5px 0 6px;
		font-size:16px;
		line-height:24px;
		color:$color-nightrider;
		@include font-family($font-semibold);
		// 20200309 START 이상현 - 스크롤 바 제거
		@include screen(custom, min, $min-md){
			padding-left:42px;
			[dir="rtl"] & {
				padding-left:0;
				padding-right:42px;
			}
		}
		// 20200309 END 이상현 - 스크롤 바 제거
		@include screen(custom, max, $max-sm){
			padding:0;
			font-size:15px;
			line-height:30px;
		}
	}
	.cookie-permit-msg {
		padding:0 !important;
		.info-icon {
			display:none;
		}
		.info-text {
			font-size:14px;
			line-height:20px;
		}
	}
}