// for debug (gulp only)
// common.js : if(location.port == '3000') $('body').addClass('show-grid');
body.show-grid {
	.iw_viewport-wrapper {
		&:before,
		&:after {
			content: '';
			display:block;
			width:1px;
			height:100%;
			position:fixed;
			z-index:9999;
			background:red;
			opacity:0.2;
			top:0;
		}
		@include screen(xl) {
			&:before {left:50%;margin-left:-642px;}
			&:after {right:50%;margin-right:-642px;}
		}
		@include screen(lg) {
			&:before {left:30px;}
			&:after {right:30px;}
		}
		@include screen(md) {
			&:before {left:30px;}
			&:after {right:30px;}
		}
		@include screen(custom, max, $max-sm) {
			&:before {left:24px;}
			&:after {right:24px;}
		}
	}
}

#content {
	&:focus {outline:none;}
}

@include screen(custom, max, $max-sm) {
	body.floating-search .search-common {
		z-index:1000000;
		position:fixed !important;left:0 !important;top:0 !important;
		width:100% !important;min-width:100% !important;
		background:#fff;padding:0;margin:0;
	}
}

img.lazyload,
img.lazyloaded {
	//transition: opacity 0.1s;
	&.mobile {display:none;}
	&.pc-md {display:none;}
	@media (max-width: 1024px) {
		&.pc-lg {display:none;}
		&.pc-md {display:inline-block;}
	}
	@media (max-width: 767px) {
		&.pc {display:none;}
