.wishlist-compare {
	position:relative;
	margin-top:17px;
	// text-align:center;
	// 20200427 START 박지영 - 단어 단위로 떨어지도록 변경
	word-break:normal;
	word-wrap:normal;
	// 20200427 END
	a {
		display:inline-block;
		vertical-align:top;
		margin-left:((16px*100%)/321px);
		&:first-child {
			margin-left:0;
		}
		[dir="rtl"] & {
			margin-left:0;
			margin-right:((16px*100%)/321px);
			&:first-child {
				margin-right:0;
			}
		}
		@include screen(custom, max, $max-md) {
			margin:0;
		}
	}
	.link-text {
		text-transform:uppercase;
		@include screen(custom, max, $max-sm) {
			&.ico-compare {
				span:first-child {
					width:auto;
					height:auto;
				}
			}
		}
	}
	.go-compare-sticky {
		display:none;
		font-size:14px;
		color:$color-dimgray;
		text-decoration: none;
		@include font-family($font-bold);
		&:focus {
			display: block;
			padding: 5px 10px;
			position: absolute;
			width: 100%;
			height:auto;
			text-align: center;
			z-index: 2;
			clip: initial;
			background: #fff;
			margin: -29px 0 0 0;
		}
	}
	.js-compare.added + .go-compare-sticky {
		display: block;
		position: absolute;
	}
}
@include screen(custom, max, $max-sm) {
	.js-compare .visible-xs {
		display:inline !important;
	}
}


