// Colorchip tooltip
.tooltip-box{
	position: absolute;
	display:block;
	background:red;
	font-size: 14px;
	padding: 5px 11px;
	border: 1px solid $line-gray;
	background: $color-white;
	color: $color-nightrider;
	border-radius: 2px;
	z-index: 9999;
	box-shadow: 0 5px 5px rgba(0, 0, 0, 0.05);
	.arrow{
		display: inline-block;
		position: absolute;
		left: 50%;
		bottom:-8px;
		width: 15px;
		height: 15px;
		margin-left:-7px;
		border: 1px solid $line-gray;
		background:$color-white;
		border-left: 0;
		border-top: 0;
		transform:rotate(45deg);
		-webkit-transform:rotate(45deg);
		-moz-transform:rotate(45deg);
		z-index: 1;
	}
	.text{
		position: relative;
		z-index: 2;
	}

}

