			margin-top:-4px!important;
		}
	}
	//  Shoppilot review (ru)
	&.rating-ru-box {
		a {
			text-decoration:none;
		}
		.sp-listing-inline-rating-widget {
			top:auto;
			display:block;
			font-size:16px;
			.sp-inline-rating-stars {
				margin-right:0;
				display:inline-block;
				.sp-star-on,
				.sp-star-off,
