/* popover */
.popover-wrap {
	// variable
	$message-height:161px;
	$bleet-size:10px;

	position:absolute;
	z-index: 500;
	.popover-inner {
		// width:360px;
		// @include screen(custom, max, $max-sm) {
		// 	width:300px;
		// }
		background-color:$bg-white;
		padding:19px 34px 19px 24px;
		border:1px solid $line-gray;
		border-radius:3px;
		text-align:left;
		[dir="rtl"] & {text-align:right;}
		&:before {
			content:"";
			position:absolute;
			width:$bleet-size; height:$bleet-size;
			background: #fff;
			border-color:inherit;
			border-style: solid;
			border-width: 0 0 1px 1px;
		}
	}
	.btn-cover {
		position: absolute;
		right:13px; top:13px;
		[dir="rtl"] & {
			right:auto;
			left:13px;
		}
		button {
			width:12px;
			height:12px;
			background : {
				image:url(/lg5-common-gp/images/common/icons/cookie-popover-close.svg);
				repeat:no-repeat;
				position:50% 50%;
				color:transparent;
			}
			border:none;
			cursor: pointer;
		}
	}
	// variation
	&.above {
		//top:-#{$message-height + floor($bleet-size/2)};
		bottom:7px;
		.popover-inner:before {
			bottom:-#{floor($bleet-size/2)};
			transform: rotate(-45deg);
			-o-transform: rotate(-45deg);
			-ms-transform: rotate(-45deg);
			-moz-transform: rotate(-45deg);
			-webkit-transform: rotate(-45deg);
		}
	}
	&.below {
		//bottom:-#{$message-height + floor($bleet-size/2)};
		top:7px;
		.popover-inner:before {
			top:-#{floor($bleet-size/2)};
			transform: rotate(135deg);
			-o-transform: rotate(135deg);
			-ms-transform: rotate(135deg);
			-moz-transform: rotate(135deg);
			-webkit-transform: rotate(135deg);
		}
	}
	&.center {
		left:50%;
		transform: translateX(-50%);
		-o-transform: translateX(-50%);
		-ms-transform: translateX(-50%);
		-moz-transform: translateX(-50%);
		-webkit-transform: translateX(-50%);
		.popover-inner:before {
			left:50%; margin-left:-#{floor($bleet-size/2)};
		}
	}
	&.left {
		left:-28px;
		.popover-inner:before {
			left:24px;
		}
	}
	&.right {
		right:-28px;
		.popover-inner:before {
			right:24px;
		}
	}
	&.bottom {
		top: 100%;
		margin-top: 15px;
		@include screen(custom, max, $max-sm) {
			margin-top: 0;
		}
	}
}

