
body.modal-open {padding-right:0 !important;}
.modal {
	padding-right:0 !important;
	@include screen(custom, min, $min-md) {
		&:before {
			content:'';
			display:inline-block;
			vertical-align: middle;
			height:100%;
		}
	}
	&.modal-scroll {
		.modal-body {max-height:400px;}
		.modal-lg .modal-body {max-height:600px;}
	}
	&.modal-fix {
		&:before{
			vertical-align: top;
		}
		.modal-dialog {
			top:40px;
			@include screen(custom, max, $max-sm) {
				top:0;
			}
		}
	}
	&.modal-locked {
		overflow-y: hidden;
	}
	// alert popup
	&.modal-simple {
		.modal-header{
			padding-top:32px;
			& + .modal-body {
				padding-top: 0;
			}
			@include screen(custom, max, $max-sm){
				padding-top:22px;
			}
		}
		.modal-body {
			padding:32px 40px 0 !important;
			@include screen(custom, max, $max-sm){
				padding:22px 30px 0 !important;
			}
		}
		.modal-footer {
			padding:23px 40px 40px;
			@include screen(custom, max, $max-sm){
				padding:23px 30px 30px;
			}
		}
		.modal-dialog {
			width:400px;
			margin-left:-200px;
			border:1px solid $line-darkgray;
			@include screen(custom, max, $max-sm){
				width:310px;
				margin-left:-155px;
				margin-top:47px;
			}
			// 20200506 START 이상현 - alert popup : rtl code 제거
			// [dir="rtl"] & {
			// 	left:-50%;
			// 	margin-left:0;
			// 	margin-right:-200px;
			// 	@include screen(custom, max, $max-sm){
			// 		margin-right:-155px;
			// 	}
			// }
			// 20200506 END
		}
		.simple-content-box {
			text-align: center;
			font-size:16px;
			color:$color-nightrider;
			@include screen(custom, max, $max-sm) {
				font-size:14px;
			}
			// reset
			p {margin:0;}
			.content-paragraph {
				&:not(:first-child){
					margin-top:17px;
				}
			}
			.modal-url {
				@include font-family($font-semibold);
				word-break: break-all;
			}
			.receipt-number {
				color:$color-carmine;
			}
		}
		// help library, video tutorial
	}
