		.mCSB_container {
			.question-section {
				.star-rates {
					+ .caution {
						margin-top:5px !important;
					}
				}
			}
		}
	}
	.modal-content {
		border:0;
		border-radius:0;
		box-shadow:none;
		.modal-close {
			cursor:pointer;
			width:20px;height:20px;
			padding:0;border:0;
			overflow:hidden;
			white-space:nowrap;
			background:url('/lg5-common-gp/images/common/icons/modal-close.svg') no-repeat 0 0;
			position:absolute;
			z-index:1;
			top:22px;
			right:30px;
			text-indent:200%;
			[dir="rtl"] & {
				right:auto;
				left:30px;
				text-indent:-400%;
			}
			@include screen(custom, max, $max-sm) {
				right:20px;top:17px;
				[dir="rtl"] & {
					right:auto;
					left:20px;
				}
				width:15px;height:15px;
				background-size:100% 100%;
			}
		}

		> .modal-body {
			&:first-child {
				padding-top: 60px;
			}
		}
	}
	.modal-header {
		border-bottom:3px solid $line-carmine;
		margin:0 30px 18px;
		padding:22px 40px 10px 0;
		// padding:22px 40px 9px 0;
		[dir="rtl"] & {padding:22px 0 10px 40px;}
		@include screen(custom, max, $max-sm) {
			margin-left:20px;margin-right:20px;
			padding:12px 30px 9px 0;
			// padding:16px 30px 8px 0;
			[dir="rtl"] & {padding:12px 0 9px 30px;}
		}
		& + .modal-body {
			padding-top:0;
		}
	}
	.modal-title {
		@include font-family($font-semibold);
		font-size:28px;
		line-height:32px;
		color:$color-black;
		@include screen(custom, max, $max-sm) {
			// font-size:20px;
			// line-height:26px;
			font-size:24px;
		}
	}
	.modal-body {
		padding:0 30px;
		color:$color-dimgray;
		@include font-family($font-regular);
		@include screen(custom, max, $max-sm) {
			padding-left:20px;padding-right:20px;
		}
		p {margin:0;}
	}
	.modal-footer {
		border:0;
		padding:20px 30px 30px;
		// padding:19px 30px 42px;
		text-align:center;
		display:block;
		justify-content:center;
		@include screen(custom, max, $max-sm) {
			padding:20px;
		}
		.btn {
			margin:0 5px;
			&.btn-block {
				margin:0;
				+ .btn-block {
					margin-top:10px;
				}
			}
		}
	}
}
